import { Component } from '@angular/core';
import { AlertController, LoadingController, NavController, ToastController, Nav } from 'ionic-angular';
import { Storage } from '@ionic/storage';
import { City, CityProvider } from "../../providers/city/city";
import { Plan, PlanProvider } from "../../providers/plan/plan";
import { Questionary, QuestionaryProvider } from "../../providers/questionary/questionary";
import { DatabaseProvider } from "../../providers/database/database";
import { RestProvider } from "../../providers/rest/rest";
import { Respondent } from '../respondent-profile/respondent-profile';

@Component({
  selector: 'page-home',
  templateUrl: 'home.html'
})
export class HomePage {
  cities: City[] = [];
  userTypes: UserType[] = [];
  plans: Plan[] = [];
  questionaries: Questionary[] = [];
  btnContinueDisabled: boolean = true;
  btnCaseTestDisabled: boolean = false;
  loader: any;
  isRuralZone: boolean = false;
  useGame: boolean = false;

  constructor(public navCtrl: NavController, private toastCtrl: ToastController, private cityProvider: CityProvider, private planProvider: PlanProvider, private questionaryProvider: QuestionaryProvider, public loadingCtrl: LoadingController, private storage: Storage, private databaseProvider: DatabaseProvider, private alertCtrl: AlertController, public restProvider: RestProvider) {
    this.loader = this.loadingCtrl.create();
    this.loader.present();
    this.restProvider.checkConnectionLocalVersion(this.navCtrl.getActiveChildNav());
    this.loadUserTypes();
    this.changeZone();
    setTimeout(() => {
      this.existsAppCityPlan();
      this.loader.dismiss();
    }, 1000);
  }

  existsAppCityPlan() {
    let entities = ['city', 'plan', 'isRuralZone', 'questionaries'];
    this.databaseProvider.verifyEntities(entities)
      .then((data) => {
        if (data.length > 0 && data.filter((data) => data == false).length == 0) {
          this.storage.get('respondent')
            .then((respondent: Respondent) => {
              if (respondent != null) {
                let alert = this.alertCtrl.create({
                  title: 'Já existe um usuário respondendo os questionários',
                  message: 'Você gostaria de continuar respondendo os questionários com o usuário: <br/> CPF - ' + respondent.cpf + '?',
                  buttons: [{
                    text: "Sim",
                    handler: () => {
                      this.navigateQuestionaryList();
                    }
                  }, {
                    text: "Não",
                    handler: () => {
                      this.removeStoredData();
                    }
                  }]
                });
                alert.present();
              } else {
                this.removeStoredData();
              }
            })
            .catch(() => {
              this.removeStoredData();
            });
        } else {
          this.getAllCitiesWithPlan();
        }
      })
      .catch(() => this.getAllCitiesWithPlan());
  }

  getAllCitiesWithPlan() {
    this.cityProvider.getAllCitiesWithPlan()
      .then((cities: City[]) => {
        if (cities != null) {
          this.cities = cities;
        } else {
          this.cities = [];
          this.showAlertGetAllCitiesWithPlan();
        }
      })
      .catch(() => {
        this.cities = [];
        this.showAlertGetAllCitiesWithPlan();
      });
  }

  showAlertGetAllCitiesWithPlan() {
    let alert = this.alertCtrl.create({
      title: 'Oops!',
      message: 'Não foi possível acessar os dados do servidor. Por favor, tente novamente.',
      buttons: [{
        text: "Tentar novamente",
        handler: () => {
          this.getAllCitiesWithPlan();
        }
      }]
    });
    alert.present();
  }

  getAllPlansByCity(city) {
    this.planProvider.getAllPlansByCity(city)
      .then((plans: Plan[]) => {
        if (plans != null) {
          this.storage.set('city', city).then(() => {
            // Plano de mobilidade temporário [x]
            //-----------------------TEMPORÁRIO-----------------------
            let p: Plan = {
              city: city,
              id: 666,
              name: "Plano de Mobilidade",
              usePrioritization: false
            }
            plans.push(p);
            //-----------------------TEMPORÁRIO-----------------------
            this.plans = plans;
            this.questionaries = [];
            this.btnContinueDisabled = true;
          });
        } else {
          this.plans = [];
          this.showAlertGetAllPlansByCity(city);
        }
      })
      .catch(() => {
        this.showAlertGetAllPlansByCity(city);
      });
  }

  loadUserTypes() {
    let userType = new UserType();
    userType.id = 41;
    userType.name = "Cidadão Comum";
    this.userTypes.push(userType);
    userType = new UserType();
    userType.id = 42;
    userType.name = "Funcionário da Prefeitura";
    this.userTypes.push(userType);
    userType = new UserType();
    userType.id = 43;
    userType.name = "Pesquisador NEIRU";
    this.userTypes.push(userType);
  }

  selectUserType(userType) {
    this.storage.set('userType', userType);
  }

  showAlertGetAllPlansByCity(city) {
    let alert = this.alertCtrl.create({
      title: 'Oops!',
      message: 'Não foi possível acessar os dados do servidor. Por favor, tente novamente.',
      buttons: [{
        text: "Tentar novamente",
        handler: () => {
          this.getAllPlansByCity(city);
        }
      }]
    });
    alert.present();
  }

  getAllQuestionariesByPlan(plan) {
    this.questionaryProvider.getAllQuestionariesByPlan(plan)
      .then((questionaries: Questionary[]) => {
        if (questionaries != null) {
          this.storage.set('plan', plan);
          // Questionário temporário [x]
          //----------------------------TEMPORÁRIO---------------------------------
          // let questionaryTemp1: Questionary = {
          //   id: 666,
          //   name: "Centro - Diagnóstico Geral",
          //   answered: false,
          //   plan: plan,
          //   questions: null,
          // }
          // let questionaryTemp2: Questionary = {
          //   id: 661,
          //   name: "Centro - Diagnóstico Específico",
          //   answered: false,
          //   plan: plan,
          //   questions: null,
          // }
          // let questionaryTemp3: Questionary = {
          //   id: 660,
          //   name: "Centro - Comerciante",
          //   answered: false,
          //   plan: plan,
          //   questions: null,
          // }
          // questionaries.push(questionaryTemp1);
          // questionaries.push(questionaryTemp2);
          // questionaries.push(questionaryTemp3);
          // this.questionaries = this.questionaryProvider.resolveQuestionaryIcon(questionaries);
          //----------------------------TEMPORÁRIO---------------------------------
          this.storage.set('questionaries', this.questionaries);
          let points: number = 0;
          this.storage.set('points', points);
        } else {
          this.questionaries = [];
          this.showAlertGetAllQuestionariesByPlan(plan);
        }
      })
      .catch(() => {
        this.showAlertGetAllQuestionariesByPlan(plan);
      });
  }

  showAlertGetAllQuestionariesByPlan(plan) {
    let alert = this.alertCtrl.create({
      title: 'Oops!',
      message: 'Não foi possível acessar os dados do servidor. Por favor, tente novamente.',
      buttons: [{
        text: "Tentar novamente",
        handler: () => {
          this.getAllQuestionariesByPlan(plan);
        }
      }]
    });
    alert.present();
  }

  help() {
    let alert = this.alertCtrl.create({
      title: '<div text-center>Nos conte sobre você!</div>',
      message:
        '<div class="alert-align-center"><img class="img-alert" src="assets/imgs/publicdomainq-presenter.png"/></div>'
        + '<div class="alert-align-center"><strong>Vamos nos conhecer um pouco melhor...</strong></div>'
        + '<div text-center>Nos conte sobre você!</div>'
        + '<div text-center>Onde você mora?</div>'
        + '<div text-center>Em qual plano diretor você quer opinar?</div>',
      buttons: [{
        text: "Entendi, quero participar!",
        handler: () => {
        }
      }]
    });
    alert.present();
  }

  presentToast() {
    const toast = this.toastCtrl.create({
      message: 'Exemplo de mensagem toasting',
      duration: 3000,
      position: 'top',
      showCloseButton: true,
      closeButtonText: "X"
    });
    toast.present();
  }

  enableBtnContinue() {
    this.btnContinueDisabled = false;
  }

  changeZone() {
    this.storage.set('isRuralZone', this.isRuralZone ? 1 : 0);
  }

  navigateQuestionaryList() {
    this.loader = this.loadingCtrl.create();
    this.loader.present();
    this.navCtrl.setRoot('QuestionariesListPage', {}).then(() => this.loader.dismiss());
  }

  navigateProfilePage() {
    this.loader = this.loadingCtrl.create();
    this.loader.present();
    this.navCtrl.setRoot('RespondentProfilePage', {}).then(() => this.loader.dismiss());
  }

  removeStoredData() {
    this.storage.remove('respondent')
      .then(() => {
        this.storage.remove('plan')
          .then(() => {
            this.storage.remove('city')
              .then(() => {
                this.storage.remove('isRuralZone')
                  .then(() => {
                    this.storage.remove('questionaries')
                      .then(() => {
                        this.storage.remove('metricItems')
                          .then(() => {
                            this.storage.remove('neighborhoods')
                              .then(() => {
                                this.storage.remove('userType')
                                  .then(() => {
                                    this.storage.remove('points')
                                      .then(() => {
                                        this.storage.remove('intro')
                                          .then(() => {
                                            this.storage.remove('useGame')
                                              .then(() => {
                                                this.storage.remove('helpHome')
                                                  .then(() => {
                                                    console.log("Remoção de dados concluída");
                                                  })
                                                  .catch((error) => {
                                                    console.log(error);
                                                  });
                                              })
                                              .catch((error) => {
                                                console.log(error);
                                              });
                                          })
                                          .catch((error) => {
                                            console.log(error);
                                          });
                                      })
                                      .catch((error) => {
                                        console.log(error);
                                      });
                                  })
                                  .catch((error) => {
                                    console.log(error);
                                  });
                              })
                              .catch((error) => {
                                console.log(error);
                              });
                          })
                          .catch((error) => {
                            console.log(error);
                          });
                      })
                      .catch((error) => {
                        console.log(error);
                      });
                  })
                  .catch((error) => {
                    console.log(error);
                  });
              })
              .catch((error) => {
                console.log(error);
              });
          })
          .catch((error) => {
            console.log(error);
          });
      })
      .catch((error) => {
        console.log(error);
      });
    this.getAllCitiesWithPlan();
  }

}

export class UserType {
  id: number;
  name: string;
}



