import { HttpClient, HttpHeaders } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { Network } from "@ionic-native/network";
import { AlertController, Nav, Platform } from "ionic-angular";
import jsSHA from 'jssha';
import { Storage } from "@ionic/storage";
import { AppVersion } from "@ionic-native/app-version";
import { IntroPage } from '../../pages/intro/intro';

@Injectable()
export class RestProvider {

  headers: any;
  isConnected: boolean = true;
  // API_URL: string = "https://api.neiru.org/";
  // API_URL: string = "https://opina-ai-api.000webhostapp.com/";
  API_URL: string = "https://opina-ai-api.com/";
  // API_URL: string = "http://localhost/neiru-surveys-api/";

  constructor(public http: HttpClient, private network: Network, public alertCtrl: AlertController, public platform: Platform, public storage: Storage, private appVersionControl: AppVersion) {
    this.createHeader();
    this.connectSubscription();
    this.disconnectSubscription();
    this.checkConnection();
  }

  checkConnection() {
    this.isConnected = this.network.type != 'none';
    if (!this.isConnected) {
      this.throwConnectionError();
    } else {
      if (this.network.type == 'wifi') {
        new Promise(resolve => {
          this.http.get(this.API_URL + "check-connection.php", { headers: this.headers })
            .subscribe(data => {
              resolve(data);
            }, error => {
              resolve(error);
            });
        })
          .then((data: APIResponse) => {
            if (!!data && data != null && data.code == 200) {
            } else {
              this.throwConnectionError()
            }
          })
          .catch(() => this.throwConnectionError());
      }
    }
  }

  checkConnectionLocalVersion(nav: Nav) {
    this.isConnected = this.network.type != 'none';
    if (!this.isConnected) {
      this.throwConnectionError();
    } else {
      if (this.network.type == 'wifi') {
        new Promise(resolve => {
          this.http.get(this.API_URL + "check-connection.php", { headers: this.headers })
            .subscribe(data => {
              resolve(data);
            }, error => {
              resolve(error);
            });
        })
          .then((data: APIResponse) => {
            if (!!data && data != null && data.code == 200) {
              if (!!data.response && data.response != null) {
                let appVersion = data.response.app_version;
                let createdAt = data.response.created_at;
                this.checkLocalVersion(nav, appVersion, createdAt);
              } else {
                this.throwConnectionError();
              }
            } else {
              this.throwConnectionError()
            }
          })
          .catch(() => this.throwConnectionError());
      }
    }
  }

  throwConnectionError() {
    let alert = this.alertCtrl.create({
      title: 'Oops!',
      message: 'Sem conexão com internet! <br/> Para acessar o aplicativo é necessário ter conexão com a internet. <br/> Conecte seu dispositivo a internet e tente novamente.',
      buttons: [{
        text: "Ok",
        handler: () => {
          this.platform.exitApp();
        }
      }]
    });
    alert.present();
  }

  private checkLocalVersion(nav, app_version, created_at) {
    this.storage.get("app_version")
      .then((data) => {
        if (data != null && data.app_version != app_version || created_at != data.created_at) {
          this.appVersionControl.getVersionNumber().then((data) => {
            this.removeStoredData(nav);
            this.setNewVersion(app_version, created_at);
          }).catch((error) => {
            this.removeStoredData(nav);
            this.setNewVersion(app_version, created_at);
          });
        }
      })
      .catch((error) => {
        console.log(error);
        this.removeStoredData(nav);
        this.setNewVersion(app_version, created_at);
      });
  }

  private setNewVersion(app_version, created_at) {
    let version: AppVersionResponse = new AppVersionResponse();
    version.app_version = app_version;
    version.created_at = created_at;
    this.storage.set("app_version", version);
  }

  private connectSubscription(): void {
    this.network.onConnect()
      .subscribe(() => {
        this.isConnected = true;
      });
  }

  private disconnectSubscription(): void {
    this.network.onDisconnect()
      .subscribe(() => {
        this.isConnected = false;
        this.throwConnectionError();
      });
  }

  createHeader() {
    this.headers = new HttpHeaders();
    this.headers.append("Accept", 'application/json');
    this.headers.append("Content-Type", 'application/json');
  }

  cryptography(stringToCrypto) {
    let shaObj = new jsSHA("SHA-256", "TEXT");
    shaObj.update(stringToCrypto);
    let hash = shaObj.getHash("HEX");
    return hash;
  }

  removeStoredData(nav: Nav) {
    this.storage.remove('respondent')
      .then(() => {
        this.storage.remove('plan')
          .then(() => {
            this.storage.remove('city')
              .then(() => {
                this.storage.remove('isRuralZone')
                  .then(() => {
                    this.storage.remove('questionaries')
                      .then(() => {
                        this.storage.remove('metricItems')
                          .then(() => {
                            this.storage.remove('neighborhoods')
                              .then(() => {
                                this.storage.remove('userType')
                                  .then(() => {
                                    this.storage.remove('points')
                                      .then(() => {
                                        this.storage.remove('intro')
                                          .then(() => {
                                            this.storage.remove('useGame')
                                              .then(() => {
                                                this.storage.remove('helpHome')
                                                  .then(() => {
                                                    console.log("Remoção de dados concluída");
                                                    nav.setRoot(IntroPage, {});
                                                  })
                                                  .catch((error) => {
                                                    console.log(error);
                                                  });
                                              })
                                              .catch((error) => {
                                                console.log(error);
                                              });
                                          })
                                          .catch((error) => {
                                            console.log(error);
                                          });
                                      })
                                      .catch((error) => {
                                        console.log(error);
                                      });
                                  })
                                  .catch((error) => {
                                    console.log(error);
                                  });
                              })
                              .catch((error) => {
                                console.log(error);
                              });
                          })
                          .catch((error) => {
                            console.log(error);
                          });
                      })
                      .catch((error) => {
                        console.log(error);
                      });
                  })
                  .catch((error) => {
                    console.log(error);
                  });
              })
              .catch((error) => {
                console.log(error);
              });
          })
          .catch((error) => {
            console.log(error);
          });
      })
      .catch((error) => {
        console.log(error);
      });
  }

}

export class APIResponse {
  code: number;
  response: any;
}

export class AppVersionResponse {
  app_version: string;
  created_at: string;
}



