import { IntroPage } from './../pages/intro/intro';
import {Component, ViewChild} from '@angular/core';
import {AlertController, Nav, Platform} from 'ionic-angular';
import {StatusBar} from '@ionic-native/status-bar';
import {SplashScreen} from '@ionic-native/splash-screen';
import {RestProvider} from "../providers/rest/rest";

@Component({
  templateUrl: 'app.html'
})
export class MyApp {
  @ViewChild(Nav) nav: Nav;

  rootPage: any = IntroPage;

  constructor(public platform: Platform, public statusBar: StatusBar, public splashScreen: SplashScreen, private alertCtrl: AlertController, public restProvider: RestProvider) {
    this.initializeApp();
  }

  initializeApp() {
    this.platform.ready().then(() => {
      this.statusBar.styleDefault();
      this.splashScreen.hide();
    });
  }

  navigateQuestionaryList(){
    this.nav.setRoot('QuestionariesListPage', {});
  }

  navigateToAbout() {
    this.nav.setRoot('AboutPage', {});
  }

  navigateProfile() {
    this.nav.setRoot('RespondentProfilePage', {});
  }

  refreshData() {
    let alert = this.alertCtrl.create({
      title: 'Limpar dados do aplicativo',
      message: 'Você gostaria de limpar os dados já fornecidos e começar novamente?',
      buttons: [{
        text: "Sim",
        handler: () => {
          this.restProvider.removeStoredData(this.nav);
        }
      }, {
        text: "Não"
      }]
    });
    alert.present();

  }
}



