import {HttpClient} from '@angular/common/http';
import {Injectable} from '@angular/core';
import {City} from "../city/city";
import {RestProvider} from "../rest/rest";

@Injectable()
export class NeighborhoodProvider {

  constructor(public http: HttpClient, public restProvider: RestProvider) {
  }

  getAllNeighborhoodsByCity(city) {
    return new Promise(resolve => {
      this.http.get(this.restProvider.API_URL + "get-all-neighborhoods-by-city.php?city=" + city.id, {headers: this.restProvider.headers})
        .subscribe(data => {
          resolve(data);
        }, error => {
          resolve(error);
        });
    });
  }

}

export class Neighborhood {
  id: number;
  name: string;
  city: City
}



