import {BrowserModule} from '@angular/platform-browser';
import {ErrorHandler, NgModule} from '@angular/core';
import {IonicApp, IonicErrorHandler, IonicModule} from 'ionic-angular';
import {MyApp} from './app.component';
import {HomePage} from '../pages/home/home';
import {StatusBar} from '@ionic-native/status-bar';
import {SplashScreen} from '@ionic-native/splash-screen';
import {DatabaseProvider} from '../providers/database/database';
import {RestProvider} from '../providers/rest/rest';
import {IonicStorageModule} from "@ionic/storage";
import {CityProvider} from '../providers/city/city';
import {PlanProvider} from '../providers/plan/plan';
import {QuestionaryProvider} from '../providers/questionary/questionary';
import {NeighborhoodProvider} from '../providers/neighborhood/neighborhood';
import {QuestionProvider} from '../providers/question/question';
import {HttpClientModule} from "@angular/common/http";
import {RespondentProvider} from '../providers/respondent/respondent';
import {PrioritizationProvider} from '../providers/prioritization/prioritization';
import {AnswerProvider} from '../providers/answer/answer';
import {Network} from "@ionic-native/network";
import {AppVersion} from "@ionic-native/app-version";
import { IntroPage } from '../pages/intro/intro';

@NgModule({
  declarations: [
    MyApp,
    HomePage,
    IntroPage
  ],
  imports: [
    BrowserModule,
    IonicModule.forRoot(MyApp),
    HttpClientModule,
    IonicStorageModule.forRoot()
  ],
  bootstrap: [IonicApp],
  entryComponents: [
    MyApp,
    HomePage,
    IntroPage
  ],
  providers: [
    StatusBar,
    SplashScreen,
    {provide: ErrorHandler, useClass: IonicErrorHandler},
    DatabaseProvider,
    RestProvider,
    CityProvider,
    PlanProvider,
    QuestionaryProvider,
    NeighborhoodProvider,
    QuestionProvider,
    RespondentProvider,
    RespondentProvider,
    PrioritizationProvider,
    AnswerProvider,
    Network,
    AppVersion
  ]
})
export class AppModule {
}



