import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { Questionary } from "../questionary/questionary";
import { RestProvider } from "../rest/rest";

@Injectable()
export class QuestionProvider {

  constructor(public http: HttpClient, public restProvider: RestProvider) {

  }

  public getAllQuestionsByQuestionary(questionary, isRuralZone) {
    return new Promise(resolve => {
      //Essa api que vai ser alterada para vir com atributo Answered (true, false)
      this.http.get(this.restProvider.API_URL + "get-all-questions-by-questionary.php?questionary=" + questionary.id + "&isRuralZone=" + isRuralZone, { headers: this.restProvider.headers })
        .subscribe(data => {
          resolve(data);
        }, error => {
          resolve(error);
        });
    });
  }

}

export class Question {
  id: number;
  name: string;
  description: string;
  question: string;
  position: number;
  contextArea: string;
  contextAreaIcon: string;
  questionary: Questionary;
  isRuralZone: boolean;
  metricId: number;
  // Novos campos
  narrative: null;
  sound: null;
  useNarrative : boolean;
  showDontNow : boolean;
  useAnswerText : boolean;
}



