import { Component, ViewChild } from '@angular/core';
import { IonicPage, NavController, NavParams, LoadingController, AlertController, Slides } from 'ionic-angular';
import { HomePage, UserType } from '../home/home';
import { Storage } from '@ionic/storage';
import { QuestionaryProvider, Questionary } from '../../providers/questionary/questionary';
import { CityProvider, City } from '../../providers/city/city';
import { PlanProvider, Plan } from '../../providers/plan/plan';
import { DatabaseProvider } from '../../providers/database/database';
import { RestProvider } from '../../providers/rest/rest';
import { Respondent } from '../respondent-profile/respondent-profile';

@IonicPage()
@Component({
  selector: 'page-intro',
  templateUrl: 'intro.html',
})
export class IntroPage {
  @ViewChild(Slides) slides: Slides;
  loader: any;
  cities: City[] = [];
  userTypes: UserType[] = [];
  plans: Plan[] = [];
  questionaries: Questionary[] = [];
  isRuralZone: boolean = false;
  useGame: boolean = false;
  userType: UserType;
  city: City;
  plan: Plan;

  constructor(private cityProvider: CityProvider, private planProvider: PlanProvider, private questionaryProvider: QuestionaryProvider, public loadingCtrl: LoadingController, public navCtrl: NavController, public navParams: NavParams, private databaseProvider: DatabaseProvider, private storage: Storage, private alertCtrl: AlertController, public restProvider: RestProvider) {
    setTimeout(() => {
      this.changeZone();
      this.existsAppCityPlan();
      this.checkTrigger();
    }, 1000);
  }

  ionViewDidEnter() {
    this.checkTrigger();
  }

  checkTrigger() {
    //temporário
    // this.useGamification();
    // this.dontuseGamification();
    //Temporário
    this.storage.get('useGame').then((data) => {
      console.log("checkTrigger");
      let trigger = document.getElementById("trigger");
      console.log(trigger);
      if (data == null) {
        if (trigger == null) {
          this.useGamification();
        } else {
          this.dontuseGamification();
        }
      } else {
        if (trigger == null) {
          this.useGamification();
        }else{
          this.useGame = data;
        }
      }
    });
  }

  useGamification() {
    this.useGame = true;
    this.storage.set('useGame', this.useGame);
  }

  dontuseGamification() {
    this.useGame = false;
    this.storage.set('useGame', this.useGame);
  }

  changeZone() {
    this.storage.set('isRuralZone', this.isRuralZone ? 1 : 0);
  }

  nextSlide(event) {
    this.checkTrigger();
    if (event._touches.startX > 180) {
      this.slides.slideNext();
    } else {
      this.slides.slidePrev();
    }
  }

  skip() {
    this.checkTrigger();
    this.loader = this.loadingCtrl.create();
    this.loader.present();
    this.storage.set('intro', true).then(() => {
      this.skipHome();
    });
  }

  skipHome() {
    this.userTypes.forEach(u => {
      if (u.name == "Cidadão Comum") {
        this.userType = u;
        this.storage.set('userType', this.userType);
      }
    });
    this.cityProvider.getAllCitiesWithPlan()
      .then((cities: City[]) => {
        if (cities != null) {
          this.cities = cities;
          this.cities.forEach(c => {
            //IMPORTANTE - SELEÇÃO DA CIDADE
            if (c.name == "Itajubá") {
              this.city = c;
              this.planProvider.getAllPlansByCity(this.city)
                .then((plans: Plan[]) => {
                  if (plans != null) {
                    this.storage.set('city', this.city).then(() => {
                      this.plans = plans;
                      this.questionaries = [];
                      this.plans.forEach(p => {
                        //IMPORTANTE - SELEÇÃO DO PLANO
                        if (p.name == "Avaliação POSCOMP") {
                          this.plan = p;
                          this.questionaryProvider.getAllQuestionariesByPlan(this.plan)
                            .then((questionaries: Questionary[]) => {
                              if (questionaries != null) {
                                this.storage.set('plan', this.plan);
                                this.questionaries = questionaries;
                                this.storage.set('questionaries', this.questionaries);
                                let points: number = 0;
                                this.storage.set('points', points);
                                this.navigateProfilePage();
                              } else {
                                this.questionaries = [];
                                this.showAlertGetAllQuestionariesByPlan();
                              }
                            })
                            .catch(() => {
                              this.showAlertGetAllQuestionariesByPlan();
                            });
                        }
                      });
                    });
                  } else {
                    this.plans = [];
                    this.showAlertGetAllPlansByCity();
                  }
                })
                .catch(() => {
                  this.showAlertGetAllPlansByCity();
                });
            }
          });
        } else {
          this.cities = [];
          this.showAlertGetAllCitiesWithPlan();
        }
      })
      .catch(() => {
        this.cities = [];
        this.showAlertGetAllCitiesWithPlan();
      });
  }

  existsAppCityPlan() {
    let entities = ['city', 'plan', 'isRuralZone', 'questionaries'];
    this.databaseProvider.verifyEntities(entities)
      .then((data) => {
        if (data.length > 0 && data.filter((data) => data == false).length == 0) {
          this.storage.get('respondent')
            .then((respondent: Respondent) => {
              if (respondent != null) {
                // let alert = this.alertCtrl.create({
                //   title: 'Já existe um usuário respondendo os questionários',
                //   message: 'Você gostaria de continuar respondendo os questionários com o usuário: <br/> CPF - ' + respondent.cpf + '?',
                //   buttons: [{
                //     text: "Sim",
                //     handler: () => {
                      this.navigateQuestionaryList();
                //     }
                //   }, {
                //     text: "Não",
                //     handler: () => {
                //       this.removeStoredData();
                //     }
                //   }]
                // });
                // alert.present();
              } else {
                this.removeStoredData();
              }
            })
            .catch(() => {
              this.removeStoredData();
            });
        }
      });
  }

  navigateQuestionaryList() {
    this.storage.get('intro')
      .then((data) => {
        if (data != null) {
          this.loader = this.loadingCtrl.create();
          this.loader.present();
          this.navCtrl.setRoot('QuestionariesListPage', {}).then(() => this.loader.dismiss());
        }
      }).catch(() => console.log('error setting intro'));
  }

  removeStoredData() {
    this.storage.remove('respondent')
      .then(() => {
        this.storage.remove('plan')
          .then(() => {
            this.storage.remove('city')
              .then(() => {
                this.storage.remove('isRuralZone')
                  .then(() => {
                    this.storage.remove('questionaries')
                      .then(() => {
                        this.storage.remove('metricItems')
                          .then(() => {
                            this.storage.remove('neighborhoods')
                              .then(() => {
                                this.storage.remove('userType')
                                  .then(() => {
                                    this.storage.remove('points')
                                      .then(() => {
                                        this.storage.remove('intro')
                                          .then(() => {
                                            this.storage.remove('useGame')
                                              .then(() => {
                                            this.storage.remove('helpHome')
                                              .then(() => {
                                                console.log("Remoção de dados concluída");
                                              })
                                              .catch((error) => {
                                                console.log(error);
                                              });
                                            })
                                            .catch((error) => {
                                              console.log(error);
                                            });
                                          })
                                          .catch((error) => {
                                            console.log(error);
                                          });
                                      })
                                      .catch((error) => {
                                        console.log(error);
                                      });
                                  })
                                  .catch((error) => {
                                    console.log(error);
                                  });
                              })
                              .catch((error) => {
                                console.log(error);
                              });
                          })
                          .catch((error) => {
                            console.log(error);
                          });
                      })
                      .catch((error) => {
                        console.log(error);
                      });
                  })
                  .catch((error) => {
                    console.log(error);
                  });
              })
              .catch((error) => {
                console.log(error);
              });
          })
          .catch((error) => {
            console.log(error);
          });
      })
      .catch((error) => {
        console.log(error);
      });
  }

  navigateProfilePage() {

    this.navCtrl.setRoot('RespondentProfilePage', {}).then(() => this.loader.dismiss());
  }

  navigateHomePage() {
    this.storage.get('intro')
      .then((data) => {
        if (data != null) {
          this.navCtrl.setRoot(HomePage, {});
        }
      }).catch(() => console.log('error setting intro'));
  }

  showAlertGetAllCitiesWithPlan() {
    let alert = this.alertCtrl.create({
      title: 'Oops!',
      message: 'Não foi possível acessar os dados do servidor. Por favor, tente novamente.',
      buttons: [{
        text: "Tentar novamente",
        handler: () => {
          this.skipHome();
        }
      }]
    });
    alert.present();
  }

  showAlertGetAllPlansByCity() {
    let alert = this.alertCtrl.create({
      title: 'Oops!',
      message: 'Não foi possível acessar os dados do servidor. Por favor, tente novamente.',
      buttons: [{
        text: "Tentar novamente",
        handler: () => {
          this.skipHome();
        }
      }]
    });
    alert.present();
  }

  showAlertGetAllQuestionariesByPlan() {
    let alert = this.alertCtrl.create({
      title: 'Oops!',
      message: 'Não foi possível acessar os dados do servidor. Por favor, tente novamente.',
      buttons: [{
        text: "Tentar novamente",
        handler: () => {
          this.skipHome();
        }
      }]
    });
    alert.present();
  }
}



