import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { RestProvider } from "../rest/rest";

@Injectable()
export class PrioritizationProvider {

  constructor(public http: HttpClient, public restProvider: RestProvider) {

  }

  //-----------------------------IMPORTANTE-----------------------------
  public getMetricItems(metricId) {
    return new Promise(resolve => {
      //metricID : 1 - GUT | 2 - Escala qualitativa | (3-18) - Métricas do questionário de teste
      this.http.get(this.restProvider.API_URL + "get-metric-items-by-metric.php?metric=" + metricId, { headers: this.restProvider.headers })
        .subscribe(data => {
          resolve(data);
        }, error => {
          resolve(error);
        });
    });
  }
  //-----------------------------IMPORTANTE-----------------------------

  public getMetricValues() {
    return new Promise(resolve => {
      this.http.get(this.restProvider.API_URL + "get-all-metric-values.php", { headers: this.restProvider.headers })
        .subscribe(data => {
          resolve(data);
        }, error => {
          resolve(error);
        });
    });
  }

}



